import React, { useState, useEffect } from "react";
import { Form, Input, Button, Table, Select, Typography, Modal } from "antd";
import { supabase } from "./supabase"; //  supabase 

const { Option } = Select;

const App = () => {
  const [groups, setGroups] = useState([]); // 
  const [members, setMembers] = useState([]); //  member  (distinct)
  const [characters, setCharacters] = useState([]); //  member  character
  const [selectedGroup, setSelectedGroup] = useState(null); // 
  const [selectedMember, setSelectedMember] = useState(null); // 
  const [tableData, setTableData] = useState([]); // 
  const [statuses, setStatuses] = useState([]); // 
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [syncStatus, setSyncStatus] = useState(null);

  //  ParaTable 
  const fetchGroups = async () => {
    try {
      const { data, error } = await supabase
        .from("ParaTable")
        .select("para_no, para_name")
        .eq("para_type", "")
        .order("seq", { ascending: true });
      if (error) throw error;

      // 
      setGroups(data.map((row) => ({ value: row.para_no, label: row.para_name })));
    } catch (error) {
      console.error("Error fetching groups: ", error);
    }
  };

  // Fetch 
  const fetchStatuses = async () => {
    try {
      const { data, error } = await supabase
        .from("ParaTable")
        .select("para_no, para_name")
        .eq("para_type", "") // 
        .order("para_no", { ascending: true }); // 
      if (error) throw error;

      //  { value, label }
      setStatuses(data.map((status) => ({ value: status.para_no, label: status.para_name })));
    } catch (error) {
      console.error("Error fetching statuses:", error);
    }
  };

  const fetchMembers = async (groupNo) => {
    try {
      const { data, error } = await supabase
        .from("MemberData")
        .select("member") //  member
        .eq("group", groupNo)// 
        .eq("status", "Y"); // status = 'Y'; 

      if (error) throw error;

      //  DISTINCT  member
      const distinctMembers = Array.from(new Set(data.map((row) => row.member)));

      console.log("Fetched Members:", distinctMembers); // 
      setMembers(distinctMembers); // 
    } catch (error) {
      console.error("Error fetching members:", error);
    }
  };

  // 
  const fetchCharacters = async (member) => {
    try {
      const { data, error } = await supabase
        .from("MemberData")
        .select("id, group, member, character")
        .eq("member", member); // 
      if (error) throw error;

      // 
      setTableData(data.map((row) => ({ id: row.id, group: row.group, member: row.member, character: row.character })));
    } catch (error) {
      console.error("Error fetching characters: ", error);
    }
  };

  // 
  useEffect(() => {
    fetchGroups();
    fetchStatuses();
  }, []);

  // 
  const columns = [
    {
      title: "",
      dataIndex: "character",
      key: "character",
      render: (value, record, index) =>
        record.isEditable ? (
          <Input
            value={value}
            onChange={(e) => handleTableChange(e.target.value, index, "character")}
          />
        ) : (
          <span>{value}</span> // 
        ),
    },
    {
      title: "",
      dataIndex: "status",
      key: "status",
      render: (value, record, index) => (
        <Select
          placeholder=""
          value={value}
          onChange={(val) => handleTableChange(val, index, "status")}
          style={{ width: "100%" }}
        >
          {statuses.map((status) => (
            <Option key={status.value} value={status.value}>
              {status.label}
            </Option>
          ))}
        </Select>
      ),
    },
    {
      title: "",
      key: "action",
      render: (_, record, index) => (
        <div style={{ display: "flex", gap: "10px" }}>
          <Button danger onClick={() => handleRemoveRow(index)}>
            
          </Button>
        </div>
      ),
    },
  ];

  const handleTableChange = (value, index, field) => {
    const newData = [...tableData];
    newData[index][field] = value;

    //  Modal
    if (index === 0 && field === "status") {
      setSyncStatus(value); // 
      setIsModalVisible(true); // 
    }

    setTableData(newData); // 
  };

  // 
  const handleRemoveRow = (index) => {
    const newData = [...tableData];
    newData.splice(index, 1);
    setTableData(newData);
  };

  // 
  const handleAddRow = () => {
    setTableData([
      ...tableData,
      { id: Date.now(), character: "", isEditable: true }, // 
    ]);
  };

  // 
  const handleGroupChange = (value) => {
    setSelectedGroup(value);
    setSelectedMember(null);
    setTableData([]);
    fetchMembers(value); // 
  };

  // 
  const handleMemberChange = (value) => {
    setSelectedMember(value);
    fetchCharacters(value); // 
  };

  // 
  const handleSyncAll = () => {
    const newData = [...tableData];
    newData.forEach((row, i) => {
      if (i !== 0) {
        row.status = syncStatus; // 
      }
    });
    setTableData(newData); // 
    setIsModalVisible(false); //  Modal
  };

  // 
  const handleCancelSync = () => {
    setIsModalVisible(false); // 
  };

  const handleConfirm = async () => {
    // 
    const incompleteRow = tableData.find((row) => !row.status);
    if (incompleteRow) {
      alert("");
      return;
    }

    console.log('test2:', tableData);

    //  Supabase 
    const rowsToInsert = tableData.map((row) => ({
      group: row.group, // 
      type: row.status,
      member: row.member, // 
      character: row.character,
      status:'I'
    }));

    //  Supabase
    try {
      const { data, error } = await supabase.from("RowData").insert(rowsToInsert);

      if (error) {
        console.error("Error inserting data:", error);
        alert("");
        return;
      }

      alert("");
      setTableData([]); // 
    } catch (err) {
      console.error("Unexpected error:", err);
      alert("");
    }
  };

  return (
    <div
      style={{
        maxWidth: 800,
        margin: "50px auto",
        padding: "20px",
        border: "1px solid #f0f0f0",
        borderRadius: "8px",
        backgroundColor: "#fff",
      }}
    >
      <Typography.Title level={2} style={{ textAlign: "center" }}>
        
      </Typography.Title>

      <Form layout="vertical">
        <Form.Item label="" required>
          <Select
            placeholder=""
            onChange={handleGroupChange}
            value={selectedGroup}
            style={{ width: "100%" }}
          >
            {groups.map((group) => (
              <Option key={group.value} value={group.value}>
                {group.label}
              </Option>
            ))}
          </Select>
        </Form.Item>

        {selectedGroup && (
          <Form.Item label="" required>
            <Select
              placeholder=""
              onChange={handleMemberChange}
              value={selectedMember}
              showSearch
              style={{ width: "100%" }}
              optionFilterProp="children"
            >
              {members.map((member) => (
                <Option key={member} value={member}>
                  {member}
                </Option>
              ))}
            </Select>
          </Form.Item>
        )}
      </Form>

      {selectedMember && (
        <>
          <Typography.Title level={3}></Typography.Title>
          <Table
            dataSource={tableData}
            columns={columns}
            rowKey="id"
            pagination={false}
            bordered
          />
          {/*  
          <Button
            type="dashed"
            onClick={handleAddRow}
            style={{ marginTop: 16, width: "100%" }}
          >
            
          </Button>
          */}
          <Button
            type="primary"
            onClick={handleConfirm}
            style={{ marginTop: 16, width: "100%" }}
          >
            
          </Button>
        </>
      )}
      <Modal
        title=""
        open={isModalVisible} // 
        onOk={handleSyncAll} // 
        onCancel={handleCancelSync} // 
        okText=""
        cancelText=""
      >
        <p></p>
      </Modal>
    </div>

  );
};

export default App;
